body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: lightsteelblue;
    height: 100%;
}

html, body, .container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    flex-wrap: wrap;
    
}

.movies .movie {
    display: flex;
    width: 35%;
    box-sizing: border-box;
    margin: 50px;
    background-color: white;
    box-shadow: 5px 10px 5px gray;
}

.movies .movie img{
    height: 150px;
    position: relative;
    right: 10px;
    top: 10px;
}

.movies .movie .movie__data {
    padding: 10px;
}

.movies .movie .movie__data .movie__genres {
    display: flex;
    list-style: none;
    font-size: 12px;
}

.movies .movie .movie__data .movie__summary {
    font-weight: 100;
    color: #565758;
}